<template>
    <div v-if="parameter_active" class="d-flex justify-content-center align-items-center" id="config-screen">
        <div class="col-md-6 p-2 m-2 rounded" :style="{'color': $store.state.c4, 'background-color': $store.state.c2}">
            <h5 class="text-center mb-5">
                <i class="fa-solid fa-gear"></i> Paramètre
            </h5>
            <div class="p-2 mb-3">
                <p class="m-0 font-weight-bold text-center">{{ user_data.username }}</p>
                <p class="m-0 text-center">{{ user_data.email }}</p>
            </div>
            <div class="">
                <div class="m-2">
                    <router-link to="/" class="btn btn-block" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                        <i class="fa-solid fa-house"></i> Aller à l'accueil
                    </router-link>
                </div>

                <div class="m-2">
                    <Btn :c1="$store.state.cdanger" :c2="$store.state.c4" :text="'Annuler'" :cl="'fa-solid fa-xmark'" class="btn-block" @click="state_parameter(false)" />
                </div>
            </div>
        </div>
    </div>
    <div v-if="status == 'loading_page'" class="">
        <div class="py-5 d-flex flex-column justify-content-center align-items-center" :style="{'color': $store.state.c4}">
            <div class="spinner-border" role="status">
                <span class="sr-only">Loading...</span>
            </div>
            <p class="text-center m-2 font-weight-bold">Chargement du jeu <i class="fa-solid fa-mug-hot"></i></p>
        </div>
    </div>
    <div v-else-if="status == 'list_old_games'">
        <OldGames :old_games="old_games" :page="page" :nb_games="nb_games" @oldgames="get_old_games" :order="order" @continuegame="continue_game" @startgame="start_new_game()" />
    </div>
    <div v-else-if="status == 'loading'" class="">
        <div class="py-5 d-flex flex-column justify-content-center align-items-center" :style="{'color': $store.state.c4}">
            <div class="spinner-border" role="status">
                <span class="sr-only">Loading...</span>
            </div>
            <p class="text-center m-2 font-weight-bold">Commencement de la partie <i class="fa-solid fa-wand-magic-sparkles"></i></p>
        </div>
    </div>
    <div v-else-if="status == 'error'" class="">
        <div class="py-5 d-flex flex-column justify-content-center align-items-center" :style="{'color': $store.state.cdanger}">
            <p class="text-center m-0 my-3">Une erreur s'est produite <i class="fa-regular fa-face-sad-tear"></i></p>
            <router-link to="/" class="btn btn-sm" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                <i class="fa-solid fa-house"></i> Aller à l'accueil
            </router-link>
        </div>
    </div>
    <div v-else class="">
        <div class="d-flex justify-content-between align-items-center" :style="{'background-color': $store.state.c2}">
            <Btn :cl="'fa-solid fa-gear'" :style="{'visibility': 'hidden'}" />
            <p class="font-weight-bold text-center p-0 m-0" :style="{'color': $store.state.c4}">Mot n°{{game.nb_word}}</p>
            <Btn :c1="$store.state.c2" :c2="$store.state.c4" :cl="'fa-solid fa-gear'" @click="state_parameter(true)" />
        </div>

        <div class="">
            <div class="" :style="{'color': $store.state.c4}">
                <p class="m-0 my-2 d-flex justify-content-end">
                    <span>
                        <span class="font-weight-bold">SCORE : </span>
                        <span>{{game.score}} {{message.about == "right_word" ? "+ " + game.points : ""}}</span>
                    </span>
                </p>
            </div>

            <div class="d-flex justify-content-end">
                <div class="">
                    <span v-for="heart in 6" :key="heart" class="heart" :style="{'color': $store.state.cheart}">
                        <i v-if="heart <= game.current_life" class="fa-solid fa-heart"></i>
                        <i v-else class="fa-regular fa-heart"></i>
                    </span>
                </div>
            </div>

            <div class="" :style="{'color': $store.state.c4}">
                <p class="m-0 d-flex flex-column align-items-start">
                    <span><i class="fa-regular fa-star"></i> <span class="font-weight-bold">Points à gagner :</span> {{game.points}}</span>
                    <span v-if="game.combo > 0" class="badge" :style="{'background-color': $store.state.c2, 'color': $store.state.c4}">Combo : {{game.combo}}</span>
                </p>
            </div>
        </div>

        <div class="">
            <div class="my-2 d-flex justify-content-center">
                <span class="badge" :style="{'background-color': $store.state.c4, 'color': $store.state.c1}"><i class="fa-regular fa-hand-point-right"></i> {{game.keyword}}</span>
            </div>
            <div class="mb-3">
                <span v-for="word, i in formatted_word" :key="i" class="word-container d-flex justify-content-center">
                    <span v-for="letter, j in word" :key="j" class="letter d-flex justify-content-center align-items-center font-weight-bold" 
                        :class="{'alphanumeric': letter == '*' || letter.toLowerCase() != letter.toUpperCase()}"
                        :style="letter_style"
                    >
                        <span class="letter-letter p-0 m-0">{{letter == '*' ? '' : letter}}</span>
                    </span>
                </span>
            </div>
        </div>

        <Message :about="message.about" :data="message.data" @restart="do_restart()" @next="go_to_next_word()" />

        <div class="d-flex justify-content-center btns-container py-3 rounded" :style="{'background-color': $store.state.c2}">
            <Btn v-for="l in letters" :key="l" :c1="get_btn_c1(l)" :c2="$store.state.c1" :text="l" class="button-letter font-weight-bold" :is_disabled="is_btn_disabled(l)" @click="click_letter(l)" />
        </div>

        <div class="mb-2"></div>
    </div>
</template>

<script>
import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'
import Message from '@/components/game/elements/Message.vue'
import OldGames from '@/components/game/OldGames.vue'

export default {
    components: {
        Btn,
        Message,
        OldGames
    },
    props: {
        mode: {
            type: String,
            default: "solo"
        }
    },
    data () {
        return {
            game: {
                MAX_LIFE: 6,
                current_life: 6,
                combo: 0,
                points: 0,
                score: 0,
                nb_word: 1,
                word: "",
                rights: [],
                wrongs: [],
                keyword: ''
            },
            letters: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ',
            parameter_active: false,
            status: "loading_page",
            salt: '',
            game_id: 0,
            btn_disabled: false,
            message: {
                about: "any",
                data: {
                    nb: 0,
                    letter: '',
                    combo: 0,
                    word_name: "",
                    word_meaning: ""
                }
            },
            new_game: {},
            user_data: {
                "username": "",
                "email": ""
            },
            screen: {
                width: window.screen.width,
                height: window.screen.height
            },
            old_games: [],
            page: {
                current: 1,
                max_page: 1
            },
            nb_games: 1,
            order: "date_desc"
        }
    },
    computed: {
        formatted_word() {
            let max_len = this.get_max_len_word_line()
            let words = []
            for (let w of this.game.word.split(" ")) {
                words.push(w)
            }
            let words_for_game = []
            let words_line = ''
            for (var i = 0; i < words.length; i++) {
                if (!words_line) {
                    words_line = words[i]
                }
                else if (words_line.length + words[i].length + 1 <= max_len) {
                    words_line += ' ' + words[i]
                }
                else {
                    words_for_game.push(words_line)
                    words_line = words[i]
                }
                if (i == words.length - 1) {
                    words_for_game.push(words_line)
                }
            }
            return words_for_game
        },
        letter_style() {
            let width = '7vw'
            let fontsize = '6vw'

            let max_len_line = 0
            for (let line of this.formatted_word) {
                if (line.length > max_len_line) {
                    max_len_line = line.length
                }
            }

            let width_px = this.screen.width / (max_len_line+3)
            let width_vw = width_px * 100 / this.screen.width
            width = width_vw + 'vw'

            let s = this.screen.width
            if (s > 1000) {
                fontsize = '35px'
            }
            else {
                fontsize = (width_vw * 50/100) + 'vw'
            }

            let style = {
                'width': width,
                'font-size': fontsize,
                'color': this.$store.state.c4,
                'border-color': this.$store.state.c2
            }
            return style
        }
    },
    methods: {
        start_new_game() {
            this.status = "loading"
            this.start(true, true)
            this.$store.dispatch('set_is_in_game', true)
            window.addEventListener('beforeunload', this.before_window_unload)
        },
        continue_game(game_id) {
            this.status = "loading"
            let data = {
                token: localStorage.getItem("pendu_token"),
                game_id: game_id
            }
            axios
                .post(this.$store.state.url_b+"/api/game/continue", data)
                .then(res => {
                    this.game_id = res.data.game_id
                    this.game = res.data.step
                    this.salt = res.data.salt
                    this.$store.dispatch('set_is_in_game', true)
                    window.addEventListener('beforeunload', this.before_window_unload)
                    this.status = "game"
                })
                .catch(error => {
                    console.log(error)
                    this.status = "error"
                })
                .finally(() => {

                })
        },
        get_old_games(datachild) {
            this.status = "loading_page"
            this.order = datachild.order
            let data = {
                token: localStorage.getItem("pendu_token"),
                order: datachild.order,
                page: datachild.page
            }
            axios
                .post(this.$store.state.url_b+"/api/game/get-old-games", data)
                .then(res => {
                    if (res.data.old_games.length > 0) {
                        this.old_games = res.data.old_games
                        this.page = res.data.page
                        this.nb_games = res.data.nb_games
                    }
                    this.status = "list_old_games"
                })
                .catch(error => {
                    console.log(error)
                    this.status = "error"
                })
                .finally(() => {

                })
        },
        get_max_len_word_line() {
            let max_len = Math.floor(this.screen.width / 30)
            max_len = max_len > 16 ? 16 : max_len
            return max_len
        },
        state_parameter(state = true) {
            this.parameter_active = state
        },
        get_btn_c1(letter) {
            if (this.game.rights.includes(letter)) {
                return this.$store.state.cbtnright
            }
            if (this.game.wrongs.includes(letter)) {
                return this.$store.state.cbtnwrong
            }
            return this.$store.state.c3 
        },
        is_btn_disabled(letter) {
            if (this.btn_disabled) {
                return true
            }
            if (this.game.rights.includes(letter) || this.game.wrongs.includes(letter)) {
                return true
            }
            return false
        },
        click_letter(letter) {
            this.btn_disabled = true
            this.message.about = "loading"
            let data = {
                letter: letter,
                game_id: this.game_id,
                salt: this.salt,
                token: localStorage.getItem("pendu_token")
            }
            axios
                .post(this.$store.state.url_b+"/api/game/try", data)
                .then(res => {
                    // console.log(res.data)
                    this.message = res.data.message
                    if (res.data.message.about == "error_salt") {
                        return true
                    }
                    this.game = res.data.step
                    if (["right_letter", "wrong_letter"].includes(res.data.message.about)) {
                        this.btn_disabled = false
                    }
                    if (res.data.message.about == "right_word") {
                        this.new_game = res.data.new_game
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.message.about = "error"
                    this.btn_disabled = false
                })
                .finally(() => {

                })
        },
        start(is_start = false, is_new_game = false) {
            let data = {
                token: localStorage.getItem("pendu_token"),
                mode: this.mode,
                start: is_start,
                is_new_game: is_new_game
            }
            axios
                .post(this.$store.state.url_b+"/api/game/new", data)
                .then(res => {
                    // console.log(res.data)
                    if (!is_new_game && is_start && res.data.old_games.length > 0) {
                        this.old_games = res.data.old_games
                        this.page = res.data.page
                        this.nb_games = res.data.nb_games
                        this.status = "list_old_games"
                    }
                    else {
                        this.game_id = res.data.game_id
                        this.game = res.data.step
                        this.salt = res.data.salt
                        this.status = "game"
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.status = "error"
                })
                .finally(() => {

                })
        },
        do_restart() {
            this.status = "loading"
            this.message.about = "any"
            this.btn_disabled = false
            this.start()
        },
        go_to_next_word() {
            this.message.about = "any"
            this.btn_disabled = false
            this.game = this.new_game
        },
        before_window_unload(e) {
            e.preventDefault()
            let return_value = "Vous êtes sur le point de quitter cette partie"
            e.returnValue = return_value
            return return_value
        },
        update_screen() {
            this.screen.width = window.screen.width
            this.screen.height = window.screen.height
        }
    },
    created() {
        this.start(true)
        // this.$store.dispatch('set_is_in_game', true)
        // window.addEventListener('beforeunload', this.before_window_unload)
        this.user_data.username = localStorage.getItem("pendu_username")
        this.user_data.email = localStorage.getItem("pendu_email")
    },
    mounted() {
        this.update_screen()
        window.addEventListener("resize", this.update_screen)
    },
    beforeUnmount() {
        this.$store.dispatch('set_is_in_game', false)
        window.removeEventListener('beforeunload', this.before_window_unload)
        window.removeEventListener("resize", this.update_screen)
    }
}
</script>

<style scoped>
.heart {
    font-size: 18px;
    margin: 1px;
}

.word-container {
    flex-wrap: wrap;
}

.letter {
    max-width: 60px;
    aspect-ratio: 1/1;
    margin: 1px;
}

.letter-letter {
    height: 100%;
    width: 100%;
    font-size: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.letter.alphanumeric {
    border: solid 3px;
}

.btns-container {
    flex-wrap: wrap;
}

.button-letter {
    margin: 3px;
}

#config-screen {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100dvw;
    height: 100dvh;
    overflow: auto;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);
}
</style>