<template>
    <nav aria-label="Pagination">
        <ul class="pagination">
            <li v-for="p in low_pagination" :key="p" class="page-item" :class="is_current(p)" @click="click_page(p)">
                <a class="page-link" href="#" :style="get_style_link(p)">{{p}}</a>
            </li>
            <li v-for="p in middle_pagination" :key="p" class="page-item" :class="is_current(p)" @click="click_page(p)">
                <a class="page-link" href="#" :style="get_style_link(p)">{{p}}</a>
            </li>
            <li v-for="p in high_pagination" :key="p" class="page-item" :class="is_current(p)" @click="click_page(p)">
                <a class="page-link" href="#" :style="get_style_link(p)">{{p}}</a>
            </li>
        </ul>
    </nav>
</template>

<script>
// import axios from 'axios'

export default {
    components: {
    },
    props: {
        current: {
            type: Number,
            default: 1
        },
        max: {
            type: Number,
            default: 1
        },
        space: {
            type: Number,
            default: 2
        }
    },
    data () {
        return {
            
        }
    },
    computed: {
        low_pagination() {
            if (this.current > this.space + 1) {
                if (this.current == this.space + 2) {
                    return [1]
                }
                return [1, '...']
            }
            return []
        },
        middle_pagination() {
            let lowest = this.current - this.space
            if (lowest < 1) {
                lowest = 1
            }
            let highest = this.current + this.space
            if (highest > this.max) {
                highest = this.max
            }
            let pages = []
            let i = lowest
            while (i <= highest) {
                pages.push(i)
                i += 1
            }
            return pages
        },
        high_pagination() {
            if (this.current < this.max - this.space) {
                if (this.current == this.max - this.space - 1) {
                    return [this.max]
                }
                return ['...', this.max]
            }
            return []
        },
    },
    methods: {
        is_current(page) {
            if (page == this.current) {
                return ['active']
            }
            if (page == '...') {
                return ['disabled']
            }
            return []
        },
        get_style_link(page) {
            let style = {
                'background-color': this.current == page ? this.$store.state.c3 : this.$store.state.c1,
                'color': this.current == page ? this.$store.state.c1 : this.$store.state.c4,
                'border-color': this.$store.state.c4
            }
            return style
        },
        click_page(page) {
            if (page != '...' && page != this.current) {
                this.$emit("clickpage", page)
            }
        }
    },
    created() {
        
    }
}
</script>

