<template>
    <div class="container">
        <div class="row">
            <div class="col">
                <Title :color="$store.state.c4" />
            </div>
        </div>

        <div class="row my-3" :style="{'color': $store.state.c4}">
            <div class="col">
                <h4 class="text-center">Classement des joueurs</h4>
            </div>
        </div>

        <div v-if="status == 'loading'" class="d-flex flex-column justify-content-center align-items-center" :style="{'color': $store.state.c4}">
            <div class="spinner-border" role="status">
                <span class="sr-only">Loading...</span>
            </div>
            <p class="text-center m-2 font-weight-bold">Chargement</p>
        </div>

        <div v-else-if="status == 'error'" class="row" :style="{'color': $store.state.cdanger}">
            <div class="col d-flex flex-column align-items-center">
                <p class="text-center m-0 mb-3">Une erreur s'est produite</p>
                <router-link to="/" class="btn" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                    <i class="fa-solid fa-house"></i> Aller à l'accueil
                </router-link>
            </div>
        </div>

        <div v-else class="" :style="{'color': $store.state.c4}">
            <router-link to="/" class="btn btn-sm mb-3" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                <i class="fa-solid fa-house"></i> Aller à l'accueil
            </router-link>

            <p class="font-weight-bold">Vous</p>
            <div v-if="self_rank" class="row">
                <div class="col">
                    <div class="container">
                        <div class="row rank-row rounded" :style="{'border-color': $store.state.c4}">
                            <div class="col-2 d-flex justify-content-center align-items-center" :style="get_num_style(self_rank.user_id)">
                                <span class="font-weight-bold rank-num">#{{self_rank.rank}}</span>
                            </div>
                            <div class="col-6 d-flex align-items-center">
                                <p class="m-0 font-weight-bold username">{{self_rank.username}} (Vous)</p>
                            </div>
                            <div class="col-4 d-flex flex-column align-items-center py-2">
                                <span class="font-weight-bold score-text">{{self_rank.score}}</span>
                                <span class="badge" :style="{'background-color': $store.state.cheart}">meilleur score</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-else class="row">
                <div class="col">
                    <p class="m-0 text-center font-italic" :style="{'color': $store.state.c4}">Jouez pour entrer dans le classement</p>
                </div>
            </div>

            <hr class="my-4" :style="{'background-color': $store.state.c2}">

            <p class="font-weight-bold">Tous les joueurs</p>
            <div class="row">
                <div class="col">
                    <div class="container">
                        <div v-for="rank in ranking" :key="rank.user_id" class="row rank-row rounded mb-1" :style="{'border-color': $store.state.c4}">
                            <div class="col-2 d-flex justify-content-center align-items-center" :style="get_num_style(rank.user_id)">
                                <span class="font-weight-bold rank-num">#{{rank.rank}}</span>
                            </div>
                            <div class="col-6 d-flex align-items-center">
                                <p class="m-0 font-weight-bold username">{{rank.username}} <span v-if="self_rank && self_rank.user_id == rank.user_id">(Vous)</span></p>
                            </div>
                            <div class="col-4 d-flex flex-column align-items-center py-2">
                                <span class="font-weight-bold score-text">{{rank.score}}</span>
                                <span class="badge" :style="{'background-color': $store.state.cheart}">meilleur score</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="mb-5"></div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
// import Btn from '@/components/elements/Btn.vue'
import Title from '@/components/elements/Title.vue'

export default {
    components: {
        // Btn,
        Title,
    },
    props: {
        
    },
    data () {
        return {
            status: "loading",
            ranking: [],
            self_rank: {}
        }
    },
    methods: {
        get_num_style(user_id) {
            let style = {'background-color': this.$store.state.c4, 'color': this.$store.state.c1}
            if (!this.self_rank) {
                return style
            }
            if (user_id == this.self_rank.user_id) {
                style["background-image"] = "linear-gradient(to right, "+this.$store.state.c2+" , "+this.$store.state.c4+")"
            }
            return style
        },
        get_ranking() {
            let token = localStorage.getItem("pendu_token")
            axios
                .get(this.$store.state.url_b+"/api/game/ranking?token="+token)
                .then(res => {
                    this.ranking = res.data.ranking
                    this.self_rank = res.data.self_rank
                    this.status = "ranking"
                })
                .catch(error => {
                    console.log(error)
                    this.status = "error"
                })
                .finally(() => {

                })
        },
    },
    created() {
        this.get_ranking()
    }
}
</script>

<style scoped>
.rank-row {
    border: 1px solid;
}

.score-text {
    font-size: 1.8rem;
}

.rank-num {
    font-size: 1.2rem;
}

.username {
    word-break: break-word;
}
</style>