<template>
    <div class="" :style="{'color': $store.state.c4}">
        <div class="d-flex flex-column align-items-center">
            <p class="m-0 text-center mb-2">Voulez-vous commencer une nouvelle partie ?</p>
            <Btn :c1="$store.state.c3" :c2="$store.state.c1" :text="'Commencer une nouvelle partie'" :cl="'fa-solid fa-flag-checkered'" @click="start_new_game()" />
            <router-link to="/" class="btn btn-sm mt-3" :style="{'background-color': $store.state.cdanger, 'color': $store.state.c1}">
                <i class="fa-solid fa-house"></i> Aller à l'accueil
            </router-link>
        </div>

        <div class="mt-4">
            <p class="m-0 text-center mb-2">Continuer une ancienne partie ?</p>
            <div class="">
                <form class="container">
                    <div class="form-row d-flex justify-content-center">
                        <div class="form-group col-md-4">
                            <label for="select-order">Trier par :</label>
                            <select class="form-control form-control-sm" id="select-order" :value="order" @change="change_order($event)">
                                <option value="date_desc">date récente vers ancienne</option>
                                <option value="date_asc">date ancienne vers récente</option>
                                <option value="score_asc">score croissant</option>
                                <option value="score_desc">score décroissant</option>
                            </select>
                        </div>
                    </div>
                </form>

                <p class="text-center m-0">Vous avez <span class="font-weight-bold">{{nb_games}} {{nb_games > 1 ? 'parties' : 'partie'}}</span> {{nb_games > 1 ? 'inachevées' : 'inachevée'}}</p>

                <div v-if="page.max_page > 1" class="mt-3">
                    <div class="d-flex justify-content-center">
                        <Pagination :current="page.current" :max="page.max_page" @clickpage="click_page" />
                    </div>
                </div>

                <div class="container">
                    <div v-for="game, i in old_games" :key="i" class="my-2 row d-flex justify-content-center">
                        <div class="col-md-6 p-2 rounded old-game" :style="{'background-color': $store.state.c2, 'border-color': $store.state.c4}">
                            <p class="font-italic m-0">Il y a {{get_date_long(game.delta_sec_update)}}</p>
                            <div class="d-flex justify-content-between">
                                <span class="">
                                    <span class="m-0 badge" :style="{'background-color': $store.state.c4, 'color': $store.state.c1}">Nombre de mots joués : {{game.nb_word}}</span>
                                </span>
                                <div class="">
                                    <span v-for="heart in game.max_life" :key="heart" class="heart" :style="{'color': $store.state.cheart}">
                                        <i v-if="heart <= game.current_life" class="fa-solid fa-heart"></i>
                                        <i v-else class="fa-regular fa-heart"></i>
                                    </span>
                                </div>
                            </div>
                            <div class="">
                                <div class="d-flex justify-content-start">
                                    <span class="badge" :style="{'background-color': $store.state.c4, 'color': $store.state.c1}"><i class="fa-regular fa-hand-point-right"></i> {{game.keyword}}</span>
                                </div>
                                <div class="d-flex justify-content-center">
                                    <span v-for="letter, i in game.hidden_word" :key="i" class="m-1">
                                        {{letter == '*' ? '_' : letter}}
                                    </span>
                                </div>
                            </div>
                            <div class="d-flex flex-column align-items-center">
                                <span class="font-weight-bold old-game-score">{{game.score}}</span>
                                <span>SCORE</span>
                            </div>
                            <div class="m-1 d-flex justify-content-center">
                                <Btn :c1="$store.state.c3" :c2="$store.state.c1" :text="'Continuer'" :is_sm="true" class="" @click="continue_game(game.id)" />
                            </div>
                        </div>
                    </div>
                </div>

                <div v-if="page.max_page > 1" class="">
                    <div class="d-flex justify-content-center">
                        <Pagination :current="page.current" :max="page.max_page" @clickpage="click_page" />
                    </div>
                </div>

                <div class="mb-3"></div>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'
import Pagination from '@/components/elements/Pagination.vue'

export default {
    components: {
        Btn,
        Pagination
    },
    props: {
        page: {
            default: {
                current: 1,
                max_page: 1
            }
        },
        old_games: {
            default: []
        },
        nb_games: {
            type: Number,
            default: 1
        },
        order: {
            type: String,
            default: "date_desc"
        }
    },
    data () {
        return {

        }
    },
    computed: {

    },
    methods: {
        get_date_long(delta) {
            let days = Math.floor(delta / (3600 * 24))
            if (days >= 1) {
                return days + (days == 1 ? ' jour' : ' jours')
            }
            let hours = Math.floor(delta / 3600)
            if (hours >= 1) {
                return hours + (hours == 1 ? ' heure' : ' heures')
            }
            let minutes = Math.floor(delta / 60)
            if (minutes >= 1) {
                return minutes + (minutes == 1 ? ' minute' : ' minutes')
            }
            return "1 minute"
        },
        click_page(page) {
            let data = {
                order: this.order,
                page: page
            }
            this.$emit("oldgames", data)
        },
        change_order(event) {
            let data = {
                order: event.target.value,
                page: this.page.current
            }
            this.$emit("oldgames", data)
        },
        continue_game(game_id) {
            this.$emit("continuegame", game_id)
        },
        start_new_game() {
            this.$emit("startgame")
        }
    },
    created() {

    },
    beforeUnmount() {

    }
}
</script>

<style scoped>
.old-game {
    border: 2px solid;
}

.old-game-score {
    font-size: 30px;
}

.heart {
    font-size: 18px;
    margin: 1px;
}
</style>